<template>
  <div>
    <ul v-if="currentChapter">
      <li
        v-for="(item, index) in currentChapter.chapters"
        :key="index"
        @click="$router.push('/read/' + bookId + '?chapterId=' + item.id)"
      >
        {{ item.title }}
      </li>
    </ul>
  </div>
</template>

<script>
import { getChapters, getChapterContent } from "../apis/apiBook";
import { mapMutations } from "vuex";
import { Toast } from "vant";

export default {
  props: ["bookId"],

  computed: {
    currentChapter: function () {
      return this.$store.getters.currentChapter(this.bookId);
    },
  },
  methods: {
    ...mapMutations(["updateAllChapters", "updateCurrentChapterContent"]),
  },
  created() {
    !this.currentChapter &&
      getChapters(this.bookId).then((res) => {
        this.updateAllChapters(res);
      });
  },
  beforeRouteLeave(to, from, next) {
    // ...

    if (to.name == "Read") {
      Toast.loading({
        duration: 0,
        message: "加载中...",
        forbidClick: true,
      });

      getChapterContent(
        this.currentChapter.chapters.find(
          (item) => item.id === to.query.chapterId
        ).link
      ).then((res) => {
        this.updateCurrentChapterContent(res.data.chapter);

        Toast.clear();
        next();
      });
    } else {
      next();
    }
  },
};
</script>

