import axios from "axios";

var getBookInfo = function(bookId) {
    return axios.get("https://novel.kele8.cn/book-info/" + bookId);
};

var getBookRaletions = function(bookId) {
    return axios.get("https://novel.kele8.cn/recommend/" + bookId);
};

var getChapters = function(bookId) {
    return new Promise((resolve, reject) => {
        axios
            .get(
                "https://novel.kele8.cn/book-sources?view=summary&book=" + bookId
            )
            .then((res) => {
                var bookSourceId = res.data[0]._id;
                axios
                    .get("https://novel.kele8.cn/book-chapters/" + bookSourceId)
                    .then((res) => {
                        resolve(res.data);
                    })
                    .catch(() => reject("章节获取失败"));
            })
            .catch(() => reject("书源获取失败"));
    });
};

var getChapterContent = function(chapterLink) {
    return axios.get(
        "https://novel.kele8.cn/chapters/" + encodeURIComponent(chapterLink)
    );
};

export { getBookInfo, getBookRaletions, getChapters, getChapterContent };
