<template>
  <div class="home">
    <header class="navbar">
      <van-nav-bar>
        <template #left>
          <van-image
            width="78"
            height="18"
            src="data:image/png;base64,"
          />
        </template>
        <template #title>
          <button
            :class="{ active: gender === 'male' }"
            @click="$router.push('/?gender=male')"
          >
            男生
          </button>
          <button
            :class="{ active: gender === 'female' }"
            @click="$router.push('/?gender=female')"
          >
            女生
          </button>
        </template>
        <template #right>
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </template>
      </van-nav-bar>
      <div class="search">
        <div class="search-box">
          <van-swipe
            style="height: 30px"
            :loop="true"
            :autoplay="3000"
            vertical
            :show-indicators="false"
          >
            <van-swipe-item>农女福妃名动天下</van-swipe-item>
            <van-swipe-item>代号修罗</van-swipe-item>
            <van-swipe-item>从签到开始当千亿大佬</van-swipe-item>
            <van-swipe-item>重生年代文孤女有空间</van-swipe-item>
          </van-swipe>
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </div>
      </div>
    </header>
    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in bannersMale" :key="index">
          <van-image width="100vw" height="40vw" :src="item.image"> </van-image>
        </van-swipe-item>
      </van-swipe>

      <van-grid :border="false">
        <van-grid-item
          v-for="(item, index) in bannerNavs"
          :key="index"
          :text="item.text"
          @click="$router.push('/mybook')"
        >
          <van-image slot="icon" width="10vw" :src="item.icon"> </van-image>
        </van-grid-item>
      </van-grid>
    </div>

    <template v-if="gender == 'male'">
      <HomeList
        v-for="item in maleList"
        :key="item.id"
        :list="item.books.slice(0, 4 )"
        >男生{{ item.shortTitle }}</HomeList
      >
    </template>

    <template v-else>
      <HomeList
        v-for="item in femaleList"
        :key="item.id"
        :list="item.books.slice(0, 5)"
        >女生{{ item.shortTitle }}</HomeList
      >
    </template>
  </div>
</template>
<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import HomeList from "@/components/HomeList.vue";
import { mapState, mapGetters, mapMutations } from "vuex";
export default {
  name: "Home",
  components: {
    HomeList,
  },
  data() {
    return {
      gender:undefined,
      maleHot: [],
      bannersMale: [
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20210209/d55ce1e6f7584005820dff309b388917.png",
        },
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png",
        },
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20210209/d57b17f1f3f7478ba1879fb631005e1a.jpg",
        },
      ],
      bannerNavs: [
        {
          icon:
            "data:image/png;base64,",
          text: "书架",
          router:'/mybook'
        },
        {
          icon:
            "data:image/png;base64,",
          text: "排行",
        },
        {
          icon:
            "data:image/png;base64,",
          text: "VIP",
        },
        {
          icon:
            "data:image/png;base64,",
          text: "充值",
        },
      ],
    };
  },
  computed: {
    ...mapState(["maleList", "rankCategory", "femaleList"]),
    ...mapGetters(["maleListIds", "femaleListIds"]),
  },
  methods: {
    ...mapMutations([
      "updateRankCategory",
      "updateMaleList",
      "updateFemaleList",
    ]),
    getListByGender: function () {
      if (this.gender === "female") {
        !this.femaleList &&
          Promise.all(
            this.femaleListIds.map((item) => {
              return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
            })
          ).then((res) =>
            this.updateFemaleList(res.map((r) => r.data.ranking))
          );
      } else {
        !this.maleList &&
          Promise.all(
            this.maleListIds.map((item) => {
              return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
            })
          ).then((res) => this.updateMaleList(res.map((r) => r.data.ranking)));
      }
    },
  },
  created() {
    this.gender = this.$route.query.gender || "male";
    if (!this.rankCategory) {
      this.axios.get("https://novel.kele8.cn/rank-category").then((res) => {
        this.updateRankCategory(res.data);
        this.getListByGender();
      });
    } else {
      this.getListByGender();
    }
  },

  beforeRouteUpdate(to, from, next) {
    //   // 在当前路由改变，但是该组件被复用时调用
    //   // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    //   // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    //   // 可以访问组件实例 `this`
      // console.log(to);
    this.gender = to.query.gender ;
    
    this.getListByGender();
    next();
  },
};
</script>
