<template>
  <div v-if="currentChapterContent">
    <h3>{{ currentChapterContent.title }}</h3>
    <p>{{ currentChapterContent.cpContent }}</p>
  </div>
</template>

<script>
import { getChapters, getChapterContent } from "../apis/apiBook";
import { mapState, mapMutations } from "vuex";

export default {
  props: ["bookId"],
  computed: {
    currentChapter: function () {
      return this.$store.getters.currentChapter(this.bookId);
    },
    ...mapState(["currentChapterContent"]),
  },
  methods: {
    ...mapMutations(["updateAllChapters", "updateCurrentChapterContent"]),
  },

  created() {
    // ...
    !this.currentChapter &&
      getChapters(this.bookId).then((res) => {
        this.updateAllChapters(res);
        getChapterContent(
          this.currentChapter.chapters[this.$route.query.index || 0].link
        ).then((res) => {
          this.updateCurrentChapterContent(res.data.chapter);
        });
      });
  },

  beforeRouteUpdate(to, from, next) {
    getChapterContent(
      this.currentChapter.chapters.find((item) => item.id == to.query.chapterId)
        .link || this.currentChapter.chapters[0].link
    ).then((res) => {
      this.updateCurrentChapterContent(res.data.chapter);
    });
    next();
  },
};
</script>

